.datetime {
  display: flex;
  background-color: #03a9f4;
  justify-content: space-between;
  align-items: center;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 40px 0;
}

.container .box {
  position: relative;
  width: 80%; /*400px;*/
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 30px;
  transition: 0.5s;
}

.container .box::before {
  content: " ";
  position: absolute;
  top: 0;
  left: 50px;
  width: 50%;
  height: 100%;
  text-decoration: none;
  background: rgb(0, 0, 0);
  border-radius: 8px;
  transform: skewX(15deg);
  transition: 0.5s;
}

.container .box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50;
  width: 50%;
  height: 100%;
  background: #fff;
  border-radius: 8px;
  transform: skewX(15deg);
  transition: 0.5s;
  filter: blur(30px);
}

.container .box:hover:before,
.container .box:hover:after {
  transform: skewX(0deg);
  left: 20px;
  width: calc(100% - 90px);
}

.container .box:nth-child(1):before,
.container .box:nth-child(1):after {
  background: linear-gradient(to right, #35be69, #11998e);
  /* background: linear-gradient(315deg, #ffbe0c, #fcff31); */
}

.container .box:nth-child(2):before,
.container .box:nth-child(2):after {
  background: linear-gradient(to left, #35be69, #11998e);
  /* background: linear-gradient(315deg, #f2c92a, #03fe86); */
}

.container .box:nth-child(3):before,
.container .box:nth-child(3):after {
  background: linear-gradient(to right, #35be69, #11998e);
  /* background: linear-gradient(315deg, #ffbe0c, #fcff31); */
}

.container .box:nth-child(4):before,
.container .box:nth-child(4):after {
  background: linear-gradient(to left, #35be69, #11998e);
  /* background: linear-gradient(315deg, #f2c92a, #03fe86); */
}

.container .box:nth-child(5):before,
.container .box:nth-child(5):after {
  background: linear-gradient(to right, #35be69, #11998e);
  /* background: linear-gradient(315deg, #ffbe0c, #fcff31); */
}

.container .box:nth-child(6):before,
.container .box:nth-child(6):after {
  background: linear-gradient(to left, #35be69, #11998e);
  /* background: linear-gradient(315deg, #f2c92a, #03fe86); */
}

.container .box span {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  pointer-events: none;
}

.container .box span::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  opacity: 0;
  transition: 0.1s;
  animation: animate 2s ease-in-out infinite;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
}

.container .box:hover span::before {
  top: -50px;
  left: 50px;
  width: 100px;
  height: 100px;
  opacity: 1;
}

.container .box span::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  opacity: 0;
  transition: 0.5s;
  animation: animate 2s ease-in-out infinite;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  animation-delay: -1s;
}

.container .box:hover span:after {
  bottom: -50px;
  right: 50px;
  width: 100px;
  height: 100px;
  opacity: 1;
}

@keyframes animate {
  0%,
  100% {
    transform: translateY(10px);
  }

  50% {
    transform: translate(-10px);
  }
}

.container .box .content {
  position: relative;
  left: 0;
  padding: 20px 40px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  z-index: 1;
  transform: 0.5s;
  color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container .box:hover .content {
  left: -25px;
  padding: 60px 40px;
}

.container .box .content h3 {
  font-size: 1.3em;
  color: rgb(0, 0, 0);
  margin-bottom: 10px;
}

.container .box .content p {
  font-size: 1.1em;
  margin-bottom: 10px;
  line-height: 1.4em;
}

.container .box .content img {
  width: 150px;
  height: 150px;
  margin-bottom: 50px;
  border-radius: 100%;
}

