
.sphere-wrapper {
    transform-style: preserve-3d;
    width: 370px;
    height: 370px;
    position: relative;
    -webkit-animation: rotate3d 10s linear infinite;
            animation: rotate3d 10s linear infinite;
  }
  .plane {
    position: absolute;
    transform-style: preserve-3d;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .spoke {
    transform-origin: 0 0;
    transform-style: preserve-3d;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 250px;
    width: 0px;
  }
  .dot {
    position: absolute;
    width: 8px;
    height: 6px;
    border-radius: 50%;
    background: #ffffff; 
    left: -3px;
    top: 100%;
    transform: rotateX(90deg);
  }
